.App-container {
  padding: 0;
  border: 0;
  margin: 0;
  background-color: #fff;
  font-weight: 400;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  width: 100%;
  height: 100%;
}

@media all and (max-width: 800px) {
  .App-container {
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
}

@media all and (max-width: 500px) {
  .App-container {
    -ms-flex-pack: column;
        justify-content: column;
  }
}

.App-container .navbar {
  padding: 0;
  border: 0;
  margin: 0;
  background-color: #fff;
  -ms-flex-order: 0;
      order: 0;
  -ms-flex-positive: 100;
      flex-grow: 100;
  -ms-flex-pack: start;
      justify-content: flex-start;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  /* border-bottom: 0.5px solid #999; */
}

.App-container .navbar-collapse {
  margin: 0;
  padding: 0;
  border: 0;
  -ms-flex-order: 0;
      order: 0;
  -ms-flex-positive: 100;
      flex-grow: 100;
  -ms-flex-item-align: start;
      align-self: flex-start;
  -ms-flex-pack: start;
      justify-content: flex-start;
  overflow: hidden;
  /* width: 100%;
  display: block!important; */
}

.App-container .navbar-brand {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  padding: 0;
  height: 65px;
}

